var lgkCookieMessage;

var lgkPill;
var lgkPillWrapper;
var lgkPillBtn;
var lgkPillContent;

const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

function init() {
    lgkCookieMessage = document.querySelector(".lgk-cookie-message");
    lgkPill = document.querySelector(".lgk-pill");
    lgkPillWrapper = document.querySelector(".lgk-pill-wrapper");
    lgkPillBtn = document.querySelector(".lgk-pill-btn");
    lgkPillContent = document.querySelector(".lgk-pill-content");
}

function isSet(variable) {
    return typeof variable != "undefined" && variable != null;
}

var LgkCookieMessage = {
    region: ((navigator.language || navigator.userLanguage).toLowerCase().indexOf("de") > -1 ? "de-DE" : "en-US"),
    setContent: function () {
        var getContent = function (legalLink) {
            if (typeof legalLink == "undefined" || legalLink == null || legalLink == "") {
                legalLink = "https://lgk.io/site/#/legal";
            }

            var region = ((navigator.language || navigator.userLanguage).toLowerCase().indexOf("de") > -1 ? "de-DE" : "en-US");
            var content = "";

            if (region.indexOf("de-") > -1) {
                content += '<div>Diese Site nutzt Cookies um Ihre Nutzererfahrung zu verbessern. ' +
                    '<a href="' + legalLink + '">Lesen Sie unsere Datenschutzerklrung.</a>';

                if (!isMobile) {
                    content += ' <br><br><a href="https://www.i-dont-care-about-cookies.eu/" target="_blank" rel="noopener">I don\'t care about cookies!</a>';
                }

                content += '</div><button>Okay, akzeptiere ich</button>';
            }
            else {
                content += '<div>This site uses cookies to improve your user experience. ' +
                    '<a href="' + legalLink + '">Read more about it.</a>';

                if (!isMobile) {
                    content += ' <br><br><a href="https://www.i-dont-care-about-cookies.eu/" target="_blank" rel="noopener">I don\'t care about cookies!</a>';
                }

                content += '</div><button>Okay, akzeptiere ich</button>';
            }

            return content;
        };

        lgkCookieMessage.innerHTML = getContent(lgkCookieMessage.getAttribute("data-legal-link"));
        lgkCookieMessage.querySelector("button").addEventListener("click", function () {
            localStorage.setItem("hide-cookie-message", "true");
            lgkCookieMessage.outerHTML = "";
        });
    }
};

LgkCookieMessage.main = function () {
    init();

    if (lgkCookieMessage) {
        if (!localStorage.getItem("hide-cookie-message")) {
            LgkCookieMessage.setContent();
        }
        else {
            lgkCookieMessage.outerHTML = "";
        }
    }
};

var LgkPill = {
    pillStateIn: false,
    lastY: null,

    hasClass: function (el, className) {
        if (el.classList)
            return el.classList.contains(className);
        else
            return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'));
    },

    addClass: function (el, className) {
        if (el.classList)
            el.classList.add(className);
        else if (!LgkPill.hasClass(el, className))
            el.className += " " + className;
    },

    removeClass: function (el, className) {
        if (el.classList)
            el.classList.remove(className);
        else if (LgkPill.hasClass(el, className))
            el.className = el.className.replace(new RegExp('(\\s|^)' + className + '(\\s|$)'), ' ');
    },

    slideIn: function () {
        init();

        LgkPill.pillStateIn = true;
        lgkPill.style.top = ".7rem";

        LgkPill.addClass(lgkPill, "in");
        LgkPill.addClass(lgkPillWrapper, "in");
        LgkPill.addClass(lgkPillBtn, "in");

        // Allows to load data only when user clicked on the pill btn
        if (lgkPillContent.getAttribute("src") == "") {
            lgkPillContent.setAttribute("src", "https://lgk.io/site/?pillMode=1");
            // lgkPillContent.setAttribute("src", "http://192.168.2.105/lgk/site/www/?pillMode=1");
        }
    },
    slideOut: function () {
        init();

        LgkPill.removeClass(lgkPill, "in");
        LgkPill.removeClass(lgkPill, "hover");
        LgkPill.removeClass(lgkPillWrapper, "in");
        LgkPill.removeClass(lgkPillBtn, "in");

        LgkPill.pillStateIn = false;
        lgkPill.style.top = -140 + "px";
    },

    /*  --- touch events --- */

    touchmove: function (event) {
        init();

        var currentY = event.touches[0].clientY;

        if (currentY > LgkPill.lastY) {
            LgkPill.slideIn();
        }
        else if (currentY < LgkPill.lastY) {
            LgkPill.slideOut();
        }

        LgkPill.lastY = currentY;
    },

    /*  --- mouse events --- */

    mouseenter: function () {
        init();

        // show just a peak
        if (LgkPill.pillStateIn) {
            lgkPill.style.top = "calc((10px - .7rem) * -1)";
        }
        else {
            lgkPill.style.top = -130 + "px";
            LgkPill.addClass(lgkPill, "hover");
        }
    },

    mouseleave: function () {
        init();

        if (LgkPill.pillStateIn) {
            LgkPill.slideIn();
        }
        else {
            LgkPill.slideOut();
            LgkPill.removeClass(lgkPill, "hover");
        }
    },

    click: function () {
        if (LgkPill.pillStateIn) {
            LgkPill.slideOut();
        }
        else {
            LgkPill.slideIn();
        }

    }
};


if (typeof module != "undefined" && module != null) {
    // is Node.js environment
    // module.exports = { LgkCookieMessage, LgkPill };

    exports.LgkCookieMessage = LgkCookieMessage;
    exports.LgkPill = LgkPill;
}
else {
    // is Browser environment
    init();

    LgkCookieMessage.main();

    lgkPillWrapper.addEventListener("click", LgkPill.click);
    lgkPillBtn.addEventListener("touchmove", LgkPill.touchmove);
    lgkPillBtn.addEventListener("mouseenter", LgkPill.mouseenter);
    lgkPillBtn.addEventListener("mouseleave", LgkPill.mouseleave);
    lgkPillBtn.addEventListener("click", LgkPill.click);
}